import React, { useState } from 'react'
import logo from './logo.svg';
import './App.css';

import Container1 from './Container';
import { Container,Grid } from 'semantic-ui-react'
import Canvas from './Canvas';
function App() {
  const today = new Date();
  const [inputs, setInputs] = useState({
    name: '', 
    birth:'930306',
    year: today.getFullYear(),
    month: today.getMonth() + 1,
    date: today.getDate(),
    attendanceYear:"2022",
    attendanceMonth:"11",
    attendanceDay:"27",
    attendanceTime:"0",
    attendanceType:"0",
    attendanceText:"1  ",
    attendanceDetailText:"1   ",
    attendanceLocation:"",
    attendMonth: today.getMonth(),
    totalDays: 19,
    attendDays: 19,
    location: '',
    classNum: 1,
    sign: '',
  })
  return (
  <>
  <Container style={{width: '1400px'}}>
    <div className="mainHeader">
      <img src={logo} className="App-logo" alt="logo" height="100px" width="100px" />
      <h1>SSAFY  </h1>
    </div>
    <Grid columns={2} stackable divided>
        <Grid.Column width={8}>
          <Container1 inputs={inputs} setInputs={setInputs}/>
        </Grid.Column>
        <Grid.Column width={8}>
          <Canvas inputs={inputs} />
        </Grid.Column>
    </Grid>
    <div className="footer">
      
      <p>&copy;  SSAFY 8  </p>
      <p> /  : dydwkd48670@gmail.com</p>
      
      <h3>
      <details>
      <summary>    </summary>
      <h4>2022.12.07  -   PDF </h4>
      <h4> </h4>
      <h5>-  </h5>
      <h5>-      </h5>
      <h5>-      </h5>
      </details>
      </h3>
      
    </div>
  </Container>

  </>
  );
}

export default App;
