import React, { useState, useEffect, useRef } from 'react'
import { Button, Input,Header, Divider,Select, Dropdown, Label } from 'semantic-ui-react'
import img from './.PNG'

const UserInput = (props) => {
  const [ctx, setCtx] = useState(null)
  const [isDrawing, setIsDrawing] = useState(false);
  const [image, setImage] = useState();
  const canvasRef = useRef();
  useEffect(() => {
    const canvas = canvasRef.current;
    const context = canvas.getContext("2d");
    context.lineWidth = 1;
    context.strokeStyle = "black";
    context.fillStyle = "rgba(255, 255, 255, 1)";
    context.fillRect(0, 0, 300, 150);

    const i = new Image();
    i.onload = () => {
      context.drawImage(i, 0, 0)
    }
    i.src = img

    setCtx(context)
  }, [])

  const onChange = (e) => {
    const { value, name } = e.target
    
    props.setInputs({
      ...props.inputs,
      [name]: value
    })
    

  }
  const handleOnChange = (e, data) => {
    const { value, name } = data
    
    props.setInputs({
      ...props.inputs,
      [name]: value
    })
 }

  const clearCanvas = () => {
    const c = canvasRef.current.getContext("2d");
    c.fillRect(0, 0, 300, 150);
    c.lineWidth = 2;
    c.strokeStyle = "black";
    c.fillStyle = "rgba(255, 255, 255, 1)";
    const i = new Image();
    i.onload = () => {
      c.drawImage(i, 0, 0)
    }
    i.src = img
    setCtx(c)
    props.setInputs({
      ...props.inputs,
      sign: i
    })
  }

  const startDraw = (event) => {
    event.persist();
    
    if(event.targetTouches){
      const rect = event.target.getBoundingClientRect();
      var offsetX = (event.touches[0].clientX - rect.left) 
      var offsetY = (event.touches[0].clientY - rect.top)
      
    }
    else{
      var { offsetX, offsetY } = event;
    }
    setIsDrawing(true);
    if (ctx) {
      ctx.beginPath();
      ctx.moveTo(offsetX, offsetY);

      }
    setImage(canvasRef.current.toDataURL())
    // props.setInputs({
    //   ...props.inputs,
    //   sign: image
    // })

  }

  const drawing = ({ nativeEvent }) => {
    if(nativeEvent.targetTouches){
      const rect = nativeEvent.target.getBoundingClientRect();
      var offsetX = (nativeEvent.touches[0].clientX - rect.left) 
      var offsetY = (nativeEvent.touches[0].clientY - rect.top)
      
      
    }
    else{
      var { offsetX, offsetY } = nativeEvent;
    }
    if (ctx) {
      if (!isDrawing) {
        ctx.beginPath();
        ctx.moveTo(offsetX, offsetY);
      } else {
        ctx.lineTo(offsetX, offsetY);
        ctx.stroke();

      }
    }
    setImage(canvasRef.current.toDataURL())
    // props.setInputs({
    //   ...props.inputs,
    //   sign: image
    // })

  }

  const stopDraw = () => {
    setIsDrawing(false);
    
    setImage(canvasRef.current.toDataURL())
    props.setInputs({
      ...props.inputs,
      sign: image
    })
  }

  const attendanceTimeOptions = [
    { key: '0', value: '0', text: '' },
    { key: '1', value: '1', text: '' },
    { key: '2', value: '2', text: '' },
  ]
  const attendanceTypeOptions = [
    { key: '0', value: '0', text: '' },
    { key: '1', value: '1', text: '' },
  ]

  return (
    <div className="">
      <div className="input-box  input-once">
        <Input label='' name="name" onChange={onChange} placeholder="" /> <br />
      </div>
      <div className="input-box">
        <div className="input-flex">
          <Input label='' name="location" onChange={onChange} placeholder="" />

          <Input label='' name="classNum" onChange={onChange} placeholder="13" className="second-input" />
        </div>
      </div>
      <div className="input-box">
        <div className="input-flex">
          <Input label='' name="birth" onChange={onChange} placeholder="990306" />
        </div>
      </div>
      <Divider />
      <div className="input-box">
        <Header as='h3'> </Header>
          <Input  name="attendanceYear" label={{ content: '' }} labelPosition='right' onChange={onChange} placeholder="2023" style={{width: '100px',marginRight: '50px'}}/>
          {/* <span>/</span> */}
          <Input  name="attendanceMonth" label={{ content: '' }} labelPosition='right'onChange={onChange} placeholder="11" className="second-input" style={{width: '50px',marginRight: '50px'}}/>
          {/* <span>/</span> */}
          <Input  name="attendanceDay" label={{ content: '' }} labelPosition='right' onChange={onChange} placeholder="27" className="second-input" style={{width: '50px',marginRight: '50px'}}/>          
      <Header as='h3'> </Header>
      <Select name="attendanceTime" onChange={handleOnChange} placeholder='' options={attendanceTimeOptions}/>

      <Header as='h3'>/ ,  </Header>
      <Input fluid labelPosition='Left' type='text' placeholder=''>
        <Label>
          <Dropdown name="attendanceType" onChange={handleOnChange} defaultValue='0' options={attendanceTypeOptions}></Dropdown>
        </Label>
        <input name="attendanceText" onChange={onChange}/>
      </Input>
      </div>

      <Header as='h3'> </Header>
      <p style={{ color: 'grey' }}>*       .</p>
      <Input fluid className='text' name="attendanceDetailText" onChange={onChange} placeholder=""  />

      <Header as='h3'> </Header>
      <p style={{ color: 'grey' }}>*     .</p>
      <Input name="attendanceLocation" onChange={onChange} placeholder="" />
      <Divider />
      <Header as='h3'> ( / )</Header>
        <p style={{ color: 'grey' }}>*    .</p>
        <div className="input-flex">
          <Input name="month" onChange={onChange} label={{ content: '' }} labelPosition='right' placeholder="13" defaultValue={props.inputs.month} />
          <span>/</span>
          <Input name="date" onChange={onChange} label={{ content: '' }} labelPosition='right' placeholder="27" defaultValue={props.inputs.date} className="second-input" />
        </div>

        <Header as='h3'></Header>
        <div className="sign-div">
          <canvas
            id="signCanvas"
            ref={canvasRef}
            style={{ border: '3px solid black',marginRight:'10px' }}
            width={166}
            height={90}
            onMouseDown={startDraw}
            onMouseUp={stopDraw}
            onMouseMove={drawing}
            onMouseLeave={stopDraw}
            onTouchStart={startDraw}
            onTouchMove={drawing}
            onTouchEnd={stopDraw}
          ></canvas>
          <Button onClick={clearCanvas}> </Button>
        </div>
    </div>
  )
  
}

export default UserInput
